import React from "react";
import styles from "./index.module.css";
import Wrapper from "../../components/UI/wrapper/index";


import { Helmet } from "react-helmet-async";
import Hero from "../../components/hero";
import Teacher from "../../components/teacher";
import Students from "../../components/students";
import Faq from "../../components/faq";
import Main from "../../components/main";
import Scroll from "../../components/scroll";

const Home = () => {
  return (
    <>
      <Helmet>
        <title>Deutsche Akademie Baku</title>
        <meta
          name="description"
          content="ETERNAL Salk Hizmetleri ile kolayca randevunuzu ayarlayn ve en iyi salk hizmetlerini deneyimleyin."
        />
        <meta
          name="keywords"
          content="Randevu, Salk Hizmetleri, ETERNAL, Online Randevu, Klinik"
        />

      </Helmet>

      <Wrapper>
        <Main/>
        <Scroll/>
        <Hero />
        <Teacher />
        <Students />
        <Faq />

      </Wrapper>
    </>
  );
};

export default Home;
